:root {
	// Verwendete Farben
	--wp--preset--color--ui-positive-100: #f3f8f1;
	--wp--preset--color--ui-positive-200: #dcebd9;
	--wp--preset--color--ui-positive-300: #c3dbbd;
	--wp--preset--color--ui-positive-500: #a8c59f;
	--wp--preset--color--ui-positive-700: #7ba66e;
	--wp--preset--color--ui-positive-900: #4b6d40;

	--wp--preset--color--ui-warning-100: #fdf1ee;
	--wp--preset--color--ui-warning-200: #fadbd5;
	--wp--preset--color--ui-warning-300: #f5a89a;
	--wp--preset--color--ui-warning-500: #e76f60;
	--wp--preset--color--ui-warning-700: #c75046;
	--wp--preset--color--ui-warning-900: #8f312b;

	--wp--preset--color--ui-info-100: #e8f1fb;
	--wp--preset--color--ui-info-200: #cde0f7;
	--wp--preset--color--ui-info-300: #9ec2f0;
	--wp--preset--color--ui-info-500: #4a90e2;
	--wp--preset--color--ui-info-700: #256bb5;
	--wp--preset--color--ui-info-900: #174075;

	--wp--preset--color--ui-neutral-100: #efefef;
	--wp--preset--color--ui-neutral-150: #dedede;
	--wp--preset--color--ui-neutral-200: #cccccc;
	--wp--preset--color--ui-neutral-300: #aaaaaa;
	--wp--preset--color--ui-neutral-400: #888888;
	--wp--preset--color--ui-neutral-500: #666666;

	--wp--preset--color--darken-100: #00000011;

	--wp--preset--border-radius-ui: 12px;
}

.wp-block-ud-toggle-container {
	background-color: var(--wp--preset--color--ui-neutral-100) !important;
	border-radius: 12px;

	.ud-toggle-switch {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 12px;
		.ud-toggle-label {
			background-color: white;
			padding: 4px 8px;
			line-height: 1;
			border-radius: 4px;
		}
		.ud-toggle-control {
			.components-flex.components-h-stack {
				gap: 0;
			}
		}
	}
}

.ud-toggle-container[data-active="one"] .ud-toggle-content > .block-editor-inner-blocks,
.ud-toggle-container[data-active="two"] .ud-toggle-content > .block-editor-inner-blocks,
.ud-toggle-container[data-active="one"] .ud-toggle-content > .block-editor-inner-blocks > .block-editor-block-list__layout > *:nth-child(1),
.ud-toggle-container[data-active="two"] .ud-toggle-content > .block-editor-inner-blocks > .block-editor-block-list__layout > *:nth-child(2){
	visibility: visible;
	position: relative;
	z-index: 1;
	opacity: 1;
	width: unset;
	height: auto;
}


.ud-toggle-container[data-active="one"] .ud-toggle-content > .block-editor-inner-blocks > .block-editor-block-list__layout > *:nth-child(2),
.ud-toggle-container[data-active="two"] .ud-toggle-content > .block-editor-inner-blocks > .block-editor-block-list__layout > *:nth-child(1){
	visibility: hidden;
	position: absolute;
	z-index: 0;
	opacity: 0;
	height: 0;
	overflow: hidden;
}